<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch">搜索</div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.fposname"
                        label="采购方式："
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showSalesWayPicker = false"
                    />
                    <van-popup v-model="showSalesWayPicker" position="bottom">
                        <van-picker show-toolbar :columns="salesWays" @cancel="showSalesWayPicker = false" @confirm="onSalesWayConfirm" />
                    </van-popup>
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.fsupplyname" label="供应商：" disabled label-align="left" label-width="2rem" />
                </van-col>
            </van-row>

            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fdeptName" label="部门：" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.director" label="主管：" disabled label-align="left" label-width="2rem" />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fempName" label="业务员：" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label="发货："
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker = true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker show-toolbar :columns="shippers" @cancel="showShipperPicker = false" @confirm="onShipperConfirm" />
                    </van-popup>
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label="保管："
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker = true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"
                        />
                    </van-popup>
                </van-col>
            </van-row>
        </div>

        <div class="form-item">
            <h2 class="form-header">物料明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.materialNumber" label="物料编码：" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.FBatchNo" label="批号：" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.fstockName" label="仓库：" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.stockPlace" label="仓位：" label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field disabled v-model="item.fQty" label="应收数量：" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="单位：" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field required v-model="item.materialActualSum" label="实收数量：" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="单位：" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field required v-model="item.NbarcodeNumber" label="条码编号：" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            NbarcodeNumber: '',
            baseData: {
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            activeName: '',
            showSalesWayPicker: false,
            salesWays: [
                // { id: 1, text: 133 },
                // { id: 2, text: 13 }
            ],
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '张三' },
                // { id: 2, text: '李四' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            materialList: []
        };
    },
    created() {
        this.showLoading('正在加载...');
        var data = this.$route.query.data;
        this.baseData.fposname = data[0].fposname;
        this.baseData.fSupplyID = data[0].fSupplyID;
        this.baseData.fsupplyname = data[0].fsupplyname;
        this.baseData.fDeptID = data[0].fDeptID;
        this.baseData.fdeptName = data[0].fdeptName;
        this.baseData.fEmpID = data[0].fEmpID;
        this.baseData.fempName = data[0].fempName;
        this.baseData.salesMan = data[0].salesMan;
        this.baseData.salesManId = data[0].salesManId;
        this.baseData.shipper = data[0].shipper;
        this.baseData.shipperId = data[0].shipperId;
        data.some((item, i) => {
            item.materialList.some((mate, j) => {
                this.materialList.push(mate);
            });
        });
        this.$api.get('/selectAllEmpInfo', {}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    this.shippers = response.data.data;
                    this.Safekeepings = response.data.data;
                    Toast.success('数据获取成功');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('数据获取异常！');
                return;
            }
        });
    },
    methods: {
        showLoading(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('正在提交数据...');
            console.log(this.NbarcodeNumber);
            this.$api.post(
                '/submitPurchaseReceipt',
                { baseData: JSON.stringify(this.baseData), materialList: JSON.stringify(this.materialList) },
                response => {
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('数据提交成功');
                            android.back();
                        } else {
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail('数据提交异常！');
                        //console.log(response);
                        return;
                    }
                }
            );
        },
        onBack() {
            this.$router.go(-1);
        },
        onSalesWayConfirm(data) {
            this.baseData.salesWay = data.text;
            this.baseData.salesWayId = data.id;
            this.showSalesWayPicker = false;
        },
        onShipperConfirm(data) {
            this.baseData.shipper = data.text;
            this.baseData.shipperId = data.id;
            this.showShipperPicker = false;
        },
        onSafekeepingConfirm(data) {
            this.baseData.safeKeeping = data.text;
            this.baseData.safeKeepingId = data.id;
            this.showSafekeepingPicker = false;
        },
        deleteMaterial() {
            this.materialList.some((item, i) => {
                if (item.id == this.activeName) {
                    this.materialList.splice(i, 1);
                    return true;
                }
            });
        },
        onSearch() {
            this.$api.get('/searchBarcodeNumber', { BarcodeNumber: this.barcodeNumber }, response => {
                if (response.status >= 200 && response.status < 300) {
                    if (response.data.success == 'true') {
                        Toast.success('条码查询成功');
                        let materialInfo = JSON.parse(response.data.msg);
                        let flag1 = true;
                        //this.NbarcodeNumber = this.barcodeNumber;
                        this.materialList.some((item, i) => {
                            if (item.stockPlace == '*') {
                                item.stockPlace = '';
                            }
                            if (item.materialNumber == materialInfo[i].mNumber && item.fstockName == materialInfo[i].warehouse) {
                                item.materialActualSum = 0;
                                this.activeName = 1;
                                console.log(this.barcodeNumber);
                                item.stockPlace == materialInfo[i].position;
                                item.materialActualSum = Number(item.materialActualSum) + Number(materialInfo[i].mSum);
                                item.NbarcodeNumber = this.barcodeNumber;
                                console.log(item.NbarcodeNumber);
                                return;
                            } else if (
                                item.materialNumber == materialInfo[i].mNumber &&
                                item.FBatchNo == materialInfo[i].batch &&
                                item.fstockName == materialInfo[i].warehouse &&
                                item.stockPlace == materialInfo[i].position
                            ) {
                                item.materialActualSum = 0;
                                this.activeName = 1;
                                item.materialActualSum = Number(item.materialActualSum) + Number(materialInfo[i].mSum);
                                item.NbarcodeNumber = this.BarcodeNumber;
                                console.log(item.NbarcodeNumber);
                                return;
                            }
                        });
                        //this.AddList();
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail('条码查询异常！');
                    return;
                }
            });
        },
        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.onSearch();
        },
        AddList() {
            let newMaterialList = this.materialList;
            newMaterialList.materialActualSum = this.materialActualSum;
            newMaterialList.stockPlace = this.stockPlace;
            this.materialList.push(newMaterialList);
        }
    }
};
</script>



